'use strict';

/**
 * @ngdoc directive
 * @name yoApp.directive:slider
 * @description
 * # slider
 */
angular.module('Q912')
  .directive('rangeslider', function () {
    return {
      templateUrl: '_rangeSlider.html',
      scope:{
          bindModel:'=?ngModel',
          defs:'=defs',
          range:'=?'
      },
      controller: function($scope){
        // check if it was defined.  If not - set a default
        $scope.bindModel = angular.isDefined($scope.bindModel) ? $scope.bindModel : true;
        $scope.off = angular.isDefined($scope.off) ? $scope.off : 'выключить';
      },
      transclude: true,
    };
  });
