'use strict';

/**
 * @ngdoc function
 * @name yoApp.controller:MainCtrl
 * @description
 * # MainCtrl
 * Controller of the yoApp
 */
angular.module('Q912')

  .controller('MainCtrl', function ($scope, Serial, $cookies, $location, FileUploader, $rootScope, $interval) {
        $scope.form = Serial.form;
        $scope.formFilter = Serial.formFilter;
        $rootScope.advMode = JSON.parse($cookies.get('advMode')||false);

        $scope.advModeToggle = function() {
          $rootScope.advMode = !$rootScope.advMode;
          $cookies.put('advMode', $scope.advMode);
          //console.log($scope.advMode);
          $scope.panel.last=$rootScope.advMode;
        };

        $scope.sendForm = Serial.sendForm;
        $scope.setState = function(key, val) {
          $scope.form[key] = val;
        };
        $scope.toggleState = function(key) {
          $scope.form[key] = !$scope.form[key];
        }

        $scope.wsConnect = function(ip) {
          Serial.connect('ip');
        }
        $scope.wsStatus = Serial.status;

        $scope.wsSend = function(message) {
          Serial.send(message);
        };
        $scope.send = Serial.send;
        $scope.isActive = function (viewLocation) {
            return viewLocation === $location.path();
        };
        $scope.path = $location.path;
        $scope.sendStop = function() {
          Serial.send('{"stop"}');
        };
        $rootScope.$on("rmFilter", function(e, id) {
          var path = id.split(".");
          //delete $scope.form[path[1]];
          $scope.formFilter[path[1]] = false;
        });
        $rootScope.$on("addFilter", function(e, id) {
          var path = id.split(".");
          $scope.formFilter[path[1]] = true;
        });

        var getData = $interval(function() {
          Serial.send('{"status"}');
        }, 3000);
        $scope.$on('$destroy', function() {
          $interval.cancel(getData);
        });

  });
