'use strict';

/**
 * @ngdoc directive
 * @name yoApp.directive:slider
 * @description
 * # slider
 */
angular.module('Q912')
  .directive('slider', function (SharedState, Serial, $rootScope) {
    return {
      templateUrl: '_slider.html',
      scope:{
          ngModel:'=',
          ngDefault:'=?',
          min: '=',
          max: '=',
          step: '=',
          title: '@',
          ngChange: '=?',
      },
      transclude: true,
      link: function(scope, element, attrs) {
        $rootScope.$broadcast("addFilter", attrs.ngModel);
        if (attrs.ngIf) $rootScope.$broadcast("addFilter", attrs.ngIf.replace("!",""));
        scope.$on('$destroy', function() {
          $rootScope.$broadcast("rmFilter", attrs.ngModel);
          if (attrs.ngIf) $rootScope.$broadcast("rmFilter", attrs.ngIf.replace("!",""));
        });
      },
      controller: function($scope) {
        if (!angular.isDefined($scope.ngModel)) $scope.ngModel=$scope.ngDefault;
      },
    };
  });
