'use strict';

/**
 * @ngdoc function
 * @name yoApp.controller:ChAkbCtrl
 * @description
 * # ChAkbCtrl
 * Controller of the yoApp
 */
angular.module('Q912')
  .controller('ChAkbCtrl', function ($scope, Serial, $interval, $routeParams, $cookies, $rootScope) {
    $scope.prof=$routeParams.prof;
    var ws=Serial;
    var formFilter = Serial.formFilter;
    $scope.form = Serial.form;

    $scope.log = Serial.log;
    ws.send({
      page: "ch_akb",
      mode:$scope.prof,
    });
    $scope.form.page = "ch_akb";
    $scope.form.mode = $routeParams.prof;

    $rootScope.$on("delete", function(e, id) {
      var path = id.split(".");
      delete $scope.form[path[1]];
    });
    $scope.$watch("form.akbU", function() {
      $scope.form.mainZU = $scope.form.akbU*1.15;
      $scope.form.mainZUl = Math.round($scope.form.akbU*1.1-1);
      $scope.form.mainZUh = Math.round($scope.form.akbU*1.25);
      $scope.form.lastZU = $scope.form.mainZU-0.5;
      if (angular.isDefined($scope.form.akbU)) ws.send({akbU: $scope.form.akbU});
    });
    $scope.$watch("form.akbAh", function() {
      $scope.form.mainZI = $scope.form.akbAh/10;
      if (angular.isDefined($scope.form.akbAh)) ws.send({akbAh: $scope.form.akbAh});
    });
    // $scope.$watch("form.mainZU", function() {
    //   $scope.form.lastZU = $scope.form.mainZU-0.5;
    // });
    $scope.sendForm = ws.sendForm;


    // $scope.form.akbAh="50";
    // $scope.form.akbAh="50";

  });
