'use strict';

/**
 * @ngdoc directive
 * @name yoApp.directive:slider
 * @description
 * # slider
 */
angular.module('Q912')
  .directive('bpanel', function ($rootScope) {
    return {
      templateUrl: '_panel.html',
      transclude: true,
      scope: {
        ngModel:'=?',
        ngDefault:'=?',
        ngOpen:'=?',
        title:'@?',
        ngState: '=?',
        inverse: '@?',
      },
      controller: function($scope){
        // check if it was defined.  If not - set a default
        if (angular.isDefined($scope.ngDefault)) $scope.ngModel=$scope.ngDefault;
        if (angular.isDefined($scope.ngOpen)) $scope.ngModel=true;
      },
      link: function(scope, element, attrs) {
        $rootScope.$broadcast("addFilter", attrs.ngModel);
        scope.$on('$destroy', function() {
          $rootScope.$broadcast("rmFilter", attrs.ngModel);
        });
      },
      // compile: function(element, attrs) {
      //
      // }
    };
  });
