'use strict';

/**
 * @ngdoc directive
 * @name yoApp.directive:focus
 * @description
 * # focus
 */
angular.module('Q912')
  .directive('focusOn', function () {
    return function(scope, elem, attr) {
          scope.$on(attr.focusOn, function(e) {
              elem[0].focus();
          });
       };
  });
