'use strict';

/**
 * @ngdoc service
 * @name yoApp.ws
 * @description
 * # ws
 * Factory in the yoApp.
 */
angular.module('Q912')
.factory('deviceReady', function(){
  return function(done) {
    if (typeof window.cordova === 'object') {
      document.addEventListener('deviceready', function () {
        done();
      }, false);
    } else {
      done();
    }
  };
});
