'use strict';

/**
 * @ngdoc function
 * @name yoApp.controller:SrvAkbCtrl
 * @description
 * # SrvAkbCtrl
 * Controller of the yoApp
 */
angular.module('Q912')
  .controller('SrvAkbCtrl', function ($scope, Serial, $interval, $routeParams, $cookies, $rootScope) {
    $scope.prof=$routeParams.prof;
    var ws=Serial;
    var formFilter = Serial.formFilter;
    $scope.form = Serial.form;
    $scope.log = Serial.log;
    ws.send({
      page: "srv_akb",
      mode:$scope.prof,
    });
    $scope.form.page = "srv_akb";
    $scope.form.mode = $routeParams.prof;

    $rootScope.$on("delete", function(e, id) {
      var path = id.split(".");
      delete $scope.form[path[1]];
      formFilter[path[1]] = false;
    });
    $scope.$watch("form.sakbU", function() {
      $scope.form.smainZU = $scope.form.sakbU*1.15;
      $scope.form.smainZUl = $scope.form.sakbU*1.067;
      $scope.form.smainZUh = $scope.form.sakbU*1.39;
      $scope.form.smainZI = $scope.form.sakbAh/10*2;

      $scope.form.slastZUl = $scope.form.smainZU*0.91;
      $scope.form.slastZUh = $scope.form.smainZU*0.99;

      $scope.form.msetDUl = $scope.form.akbU/1.1215*0.8;
      $scope.form.msetDUh = $scope.form.akbU/1.1215*1.2115;

      if (angular.isDefined($scope.form.sakbU)) {
        ws.send({sakbU: $scope.form.sakbU});

      }
    });
    $scope.$watch("form.sakbAh", function() {
      $scope.form.smainZI = $scope.form.sakbAh/10;
      if (angular.isDefined($scope.form.sakbAh)) ws.send({sakbAh: $scope.form.sakbAh});
    });
    // $scope.$watch("form.smainZU", function() {
    //   $scope.form.slastZU = $scope.form.smainZU-0.5;
    // });
    $scope.sendForm = ws.sendForm;

    // $scope.form.akbAh="50";
    // $scope.form.akbAh="50";

  });
