'use strict';

/**
 * @ngdoc function
 * @name yoApp.controller:BpCtrl
 * @description
 * # BpCtrl
 * Controller of the yoApp
 */
angular.module('Q912')
  .controller('BpCtrl', function ($scope, Serial, $interval, $routeParams, $cookies, $rootScope) {
    $scope.prof=$routeParams.prof;
    var ws=Serial;
    $scope.form = Serial.form;
    $scope.log = Serial.log;
    ws.send({
      page: "bp",
      mode:$scope.prof,
    });
    $scope.form.page = "bp";
    $scope.form.mode = $routeParams.prof;

    $rootScope.$on("delete", function(e, id) {
      var path = id.split(".");
      delete $scope.form[path[1]];
    });
    $scope.$watch("form.setUp", function() {
      if (angular.isDefined($scope.form.setUp)) ws.send({setUp: $scope.form.setUp});
    });
    $scope.$watch("form.setIp", function() {
      if (angular.isDefined($scope.form.setIp)) ws.send({setIp: $scope.form.setIp});
    });
    // $scope.$watch("form.mainZU", function() {
    //   $scope.form.lastZU = $scope.form.mainZU-0.5;
    // });
    $scope.sendForm = ws.sendForm;

    // var getData = $interval(function() {
    //   ws.send({type: "tek"});
    // }, 3000);
    // $scope.$on('$destroy', function() {
    //   $interval.cancel(getData);
    // });
    // $scope.form.akbAh="50";
    // $scope.form.akbAh="50";
  });
