'use strict';

/**
 * @ngdoc service
 * @name yoApp.ws
 * @description
 * # ws
 * Factory in the yoApp.
 * {"tek":{"tekU":12,"tekI":4}}
 */
angular.module('Q912')
  .factory('Serial', function($websocket, $window, _, $rootScope, $location) {
    var hostname=location.hostname;
    var config = {
      ip: "",
      mac: "",
      log: [],
    };
    var ws;
    var form = {
      tekU: 12,
      tekI: 6,
      srv_prof: [
        'prof1',
        'prof2',
        'prof3',
        'prof4',
        'prof5',
      ],
      ch_prof: [
        'prof1',
        'prof2',
        'prof3',
        'prof4',
        'prof5',
      ],
      bp_prof: [
        'prof1',
        'prof2',
        'prof3',
        'prof4',
        'prof5',
      ]
    };
    var collection = {};
    var formFilter = {
      page: true,
      mode: true,
    };
    var state = {
      tekU: 1,
      tekI: 1,
    }

    if (hostname === 'localhost'||hostname === '0.0.0.0'||typeof window.cordova === 'object'||window.location.protocol === 'file:') {
      // ws = connect('esp8266fs.local.', 'ws');
      ws = connect('192.168.4.1', 'ws');
    }else if (hostname === 'balsat.2d-it.ru') {
      ws = connect('192.168.4.1', 'ws');
    }else {
      ws = connect(hostname, 'ws');
    }

    ws.onMessage(function(event) {
      console.log('%c uart: '+event.data, 'background: #222; color: #bada55' );
      var res={},
        obj={};
      try {
        res = JSON.parse(event.data, function(k, v) {
          if (v === "true") return true;
          if (v === "false") return false;
          return (typeof v === "object" || isNaN(v)) ? v : parseInt(v, 10);
        });
        // console.log(res);
        if (res.page) {
          var path = "/"+res.page+"/"+res.mode;
          if ($location.path != path) $location.path(path);
        }
        Object.assign(form, res);
        ws.send("OK")
      } catch(e) {
        console.error('JSON.parse filed ', event.data);
        ws.send("ERROR: "+event.data);
      }
    });

    ws.onError(function(event) {
      console.error('connection Error', event);
    });

    ws.onClose(function(event) {
      console.info('connection closed', event);
    });

    ws.onOpen(function() {
      //console.log('connection UP', hostname);
      // ws.send('{"get":"info"}');
    });

    $window.ws=function(data) {
      ws.send(data);
    };
    $window.Serial=function(data) {
      ws.send(data);
    };
    function connect(host,path) {
      //console.log(host,path);
      return $websocket('ws://'+host+'/'+path, null, { reconnectIfNotNormalClose: true });
    };
    function setMac(mac) {
      config.mac = mac;
    }
    var filterForm = function(form) {
      return stringifyForm(_.pick(form, function(v,k,obj) { return formFilter[k] }));
    };
    function stringifyForm(form) {
      return JSON.stringify(form, function(k,v) {
        if (typeof v === 'number'||typeof v === 'boolean') return v.toString();
        return v;
      });
    };
    return {
      collection: collection,
      config: config,
      form: form,
      formFilter: formFilter,
      filterForm: filterForm,
      status: function() {
        return ws.readyState;
      },
      send: function(message) {
        // console.log(message);
        if (angular.isString(message)) {
          ws.send(message);
        }else if (angular.isObject(message)) {
          ws.send(JSON.stringify(message, function(k,v) {
            if (typeof v === 'number'||typeof v === 'boolean') return v.toString();
            return v;
          }));
        }
      },
      sendForm: function(form) {
        form.mode="run";
        // var formFiltered = _.pick(form, function(v,k,obj) {
        //   return formFilter[k];
        // });
        // ws.send(JSON.stringify(formFiltered, function(k,v) {
        //   if (typeof v === 'number'||typeof v === 'boolean') return v.toString();
        //   return v;
        // }));
        ws.send(filterForm(form));
      },
      connect: function(ip) {
        return ip;
      },
    };
  })
